.app {
    font-family: "Poppins", sans-serif;
    line-height: 1.5;
    color: var(--clr-fg);
    background: var(--clr-bg-img);
    /* background-color: var(--clr-bg); */
}

main {
    max-width: 1100px;
    width: 95%;
    margin: 0 auto;
}

.section {
    margin-top: 5em;
}

.section__title {
    text-align: center;
    margin-bottom: 0.6em;
    text-transform: uppercase;
}

.center {
    display: flex;
    align-items: center;
}

.link {
    color: var(--clr-primary);
    padding: 0 0 0.3em 0;
    position: relative;
}

.link:hover {
    color: var(--clr-primary);
}

.link::before {
    border-radius: 15px;
    content: "";
    display: inline;
    width: 0%;
    height: 0.2em;
    position: absolute;
    bottom: 0;
    background-color: var(--clr-primary);
    transition: width 0.2s ease-in;
}

.link:hover::before,
.link:focus::before {
    width: 100%;
}

.link--nav {
    color: var(--clr-fg);
    font-weight: 500;
}

.link--icon {
    color: var(--clr-fg);
}

.btn {
    display: block;
    cursor: pointer;
    padding: 0.8em 1.4em;
    font-weight: 500;
    font-size: 0.9rem;
    transition: transform 0.2s ease-in-out;
}

.btn--outline {
    color: var(--clr-primary);
    border: 2px solid var(--clr-primary);
    position: relative;
    overflow: hidden;
    z-index: 1;
}

.btn--outline:hover,
.btn--outline:focus {
    color: var(--clr-bg);
}

.btn--outline:before {
    content: "";
    position: absolute;
    background-color: var(--clr-primary);
    right: 100%;
    bottom: 0;
    left: 0;
    top: 0;
    z-index: -1;
    transition: right 0.2s ease-in-out;
}

.btn--outline:hover:before,
.btn--outline:focus:before {
    right: 0;
}

.btn--plain {
    border-radius: 25px;
    text-transform: initial;
    background-color: var(--clr-bg-alt);
    box-shadow: rgba(0, 0, 0, 0.15) 0px 3px 3px 0px;
    border: 0;
}

.btn--plain:hover {
    transform: translateY(-4px);
}

.btn--icon {
    padding: 0;
}

.btn--icon:hover,
.btn--icon:focus {
    color: var(--clr-primary);
}

.btn--icon:active {
    transform: translateY(-5px);
}

.resume-btn {
    width: 7.2em;
    margin-top: 1rem;
    border-radius: 15px;
}

.cont-btn {
    display: flex;
    margin-left: 15px;
}

.skill-tools {
    display: flex;
    justify-content: center;
}

.section__title:after {
    border-bottom: 0.2em solid var(--clr-primary);
    content: "";
    display: block;
    margin: auto;
    width: 1.5em;
    border-radius: 1.5em;
}

.noSelect {
    -webkit-tap-highlight-color: transparent;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
.noSelect:focus {
    outline: none !important;
}

@media (max-width: 600px) {
    .section {
        margin-top: 4em;
    }
    .skill-tools {
        display: block;
    }
}

.particles {
    position: absolute;
    inset: 0px;
    overflow: hidden;
}
