.btn-contact {
  display: block;
  cursor: pointer;
  padding: 0.8em 1.4em;
  font-weight: 500;
  font-size: 0.9rem;
  transition: transform 0.2s ease-in-out;
}

.mr2 {
  margin-right: 0.2em;
}

.mr5 {
  margin-right: 0.5em;
}

@media (max-width: 375px) {
  .btn-contact {
    padding: 0.8em 0.9em;
  }
  .mr5 {
    margin-right: 0.2em;
  }
}
