@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;700&display=swap");

/* Theme color variables */
[data-theme="light"] body {
    --clr-bg: #dbd6fb;
    --clr-bg-alt: #e4e0fb;
    --clr-bg-img: linear-gradient(
        to right top,
        #dae0ee,
        #d8dbf6,
        #ddd5fb,
        #e7cdfd,
        #f6c4f9
    );
    --clr-fg: #3c3b3c;
    --clr-fg-alt: #6f5e92;
    --clr-primary: #2a2438;
    --shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

[data-theme="dark"] body {
    --clr-bg: #162d36;
    --clr-bg-alt: #36555e;
    --clr-bg-img: linear-gradient(62deg, #2c5364, #203a43, #0f2027);
    --clr-fg: #dbf7ff;
    --clr-fg-alt: #bcecf8;
    --clr-primary: #00adb5;
    --shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px,
        rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}

* {
    padding: 0;
    margin: 0;
    border: 0;
    outline: 0;
    background-color: inherit;
    color: inherit;
    font-family: inherit;
    font-size: inherit;
    box-shadow: none;
    box-sizing: border-box;
}

html {
    scroll-behavior: smooth;
}

h1,
h2,
h3,
h4 {
    line-height: 1.2;
    color: var(--clr-fg-alt);
}

h1 {
    font-size: 4rem;
}

h2 {
    font-size: 2rem;
}

h3 {
    font-size: 1.5rem;
}

h4 {
    font-size: 1.3rem;
}

ul {
    list-style-type: none;
}

a {
    text-decoration: none;
}

button {
    cursor: pointer;
}

@media (max-width: 900px) {
    h1 {
        font-size: 2.6rem;
    }
}

/* animations */
.animated {
    animation-duration: 1s;
    animation-fill-mode: both;
}

@keyframes fadeInUp {
    from {
        opacity: 0;
        transform: translate3d(0, 100%, 0);
    }

    to {
        opacity: 1;
        transform: translate3d(0, 0, 0);
    }
}

.fadeInUp {
    animation-name: fadeInUp;
}

@keyframes fadeIn {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

.fadeIn {
    animation-name: fadeIn;
}

/* Scrollbar */
::-webkit-scrollbar {
    width: 0.4em;
}

::-webkit-scrollbar-track {
    background: var(--clr-bg);
}

::-webkit-scrollbar-thumb {
    background: var(--clr-fg-alt);
    border-radius: 12px;
}
